<template>
    <el-row>
        <el-row>
            <el-col :span="20">
                <el-row style="margin-bottom:6px; padding:0 60px;">
                    <el-input v-model="queryData.code" placeholder="编码" :maxlength="20" size="mini" class="inputwinth150">
                        <template slot="prepend">编码</template>
                    </el-input>
                    <el-input v-model="queryData.name" placeholder="名称" :maxlength="20" size="mini" class="inputwinth150">
                        <template slot="prepend">名称</template>
                    </el-input>
                    <el-radio v-model="queryData.is_Enable" :label="1">启用</el-radio>
                    <el-radio v-model="queryData.is_Enable" :label="0">弃用</el-radio>
                </el-row>
                <el-row>
                    <el-button size="mini" icon="el-icon-plus" @click="openDialog('新增',0)">新增</el-button>

                </el-row>
            </el-col>
            <el-col :span="4">
                <el-button size="mini" @click="cleanData" icon="fa fa-eraser"> 清 空</el-button>
                <el-button size="mini" @click="getPageData" icon="el-icon-search">查 询</el-button>
            </el-col>
        </el-row>
        <task-dialog :dialogTitle="dialogTitle" :dialogShow.sync="dialogShow" :dialogData="dialogData" @RefreshData="getPageData"></task-dialog>

        <el-table :data="tableData" :row-class-name="tableRowClassName" size="mini" v-loading="loadingdata" element-loading-text="正在加载中..." border>
            <el-table-column type="index" label="序号" width="50">
            </el-table-column>
            <el-table-column prop="code" label="编码">
            </el-table-column>
            <el-table-column prop="name" label="名称">
            </el-table-column>
            <el-table-column label="状态" width="70">
                <template slot-scope="prop">
                    <span v-html="prop.row.is_Enable==1?'启用':'弃用'"></span>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="100">
                <template slot-scope="prop">
                    <el-button @click="openDialog('编辑',prop.row.id)" type="text">编辑</el-button>
                    <el-button @click="delId(prop.row.id)" type="text">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pIndex" :page-sizes="[10,20,50,100]" :page-size="pSize" layout="total, sizes, prev, pager, next, jumper" :total="totalNum">
        </el-pagination>
    </el-row>
</template>
<script>
import taskdialog from "@/views/task/dialog/Task-Dialog";

export default {
    components: {
        "task-dialog": taskdialog
    },
    data() {
        return {
            dialogShow: false,
            dialogTitle: "",
            dialogData: { id: 0, is_Enable: 1, is_Hide: 0 },
            queryData: {},
            moduleName: "task",
            pIndex: 1,
            pSize: 10,
            totalNum: 0,
            loadingdata: false,
            tableData: [],
            roleList: [],
            deptList: [],
            importExcelShow: false,
            exportExcelShow: false,
            exportPageStr: "",
            exportWhereStr: ""
        };
    },
    created() {
        //console.log("created");
    },
    mounted() {
        //console.log("mounted");
        this.getPageData();
    },
    methods: {
        cleanData() {
            this.queryData = {};
        },
        handleSizeChange(val) {
            this.pSize = val;
            this.getPageData();
        },
        handleCurrentChange(val) {
            this.pIndex = val;
            this.getPageData();
        },
        tableRowClassName(row, rowIndex) {
            if (row.is_Enable == 0) {
                return "warning-row";
            }
            return "";
        },
        getPageData() {
            var condition = this.global.getcondition(this.queryData);
            var getpath =
                this.moduleName +
                "/findAll?pIndex=" +
                this.pIndex +
                "&pSize=" +
                this.pSize +
                condition;
            this.loadingdata = true;
            this.$ajax.get(getpath).then(
                res => {
                    this.loadingdata = false;
                    this.tableData = res.data;
                    this.totalNum = res.totalNum;
                },
                error => {
                    this.loadingdata = false;
                }
            );
        },
        openDialog(title, id) {
            this.dialogTitle = title;
            this.dialogShow = true;
            if (id > 0) {
                this.$ajax
                    .get(this.moduleName + "/findOne?id=" + id)
                    .then(res => {
                        this.dialogData = res.data;
                    });
            } else {
                this.dialogData = { id: 0, is_Enable: 1, is_Hide: 0 };
            }
        },
        delId(id) {
            this.$ajax
                .post(this.moduleName + "/deleteOne?id=" + id)
                .then(res => {
                    this.getPageData();
                });
        }
    }
};
</script>
<style scoped>
</style>



